/* CUSTOM CSS */
html,
body {
  font-family: "Source Sans Pro", sans-serif !important;
  color: #ababab !important;
  overflow-x: hidden;
  height: 100vh !important;
  min-height: 100vh !important;
  /* background-color: #f4f6f9 !important; */
}

.hov-effect:hover {
  border-bottom: 2px solid #ff2260;
}

.vertical-bar {
  border-left: 1px solid #e8e8e8;
  margin-left: 16px;
}

.content-wrapper {
  margin-left: 0px !important;
}
.container-fluid-content {
  max-width: 64rem !important;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
}

.nav-bg {
  background-color: #ffffff !important;
}
.sub-menu {
  left: 54px !important;
  min-width: 9rem !important;
}

.layout-margin {
  padding-left: 56px !important;
  padding-right: 56px !important;
}
.side-layout-margin {
  padding-left: 56px !important;
  padding-right: 56px !important;
}

.nav-bottom {
  border-bottom: 1px solid #e4e4e4;
}

.profile-image {
  padding-top: 8px;
  margin-right: 0;
  padding-right: 0;
}

.profile-info {
  padding: 5px 0px 5px 0px !important;
}

.text-live {
  color: #ff3462;
}

.red-border:hover {
  border: 1px solid #ff799d;
}

.thumbnail {
  position: relative;
  display: inline-block;
}

/***********************  CSS FOR SIDE MENU BAR START ********************/
.sidebar {
  padding-left: 0px !important;
  padding-right: 0px !important;
}

.main-sidebar {
  width: 200px !important;
}

.sidebar-mini.sidebar-collapse .main-sidebar {
  width: 70px !important;
}

body:not(.sidebar-mini-md) .main-header {
  margin-left: 200px !important;
}

body:not(.sidebar-mini-md) .content-wrapper {
  margin-left: 200px !important;
}

.sidebar-mini.sidebar-collapse .main-header {
  margin-left: 70px !important;
}

.sidebar-mini.sidebar-collapse .content-wrapper {
  margin-left: 70px !important;
}

.main-sidebar,
.sidebar {
  overflow-x: hidden !important;
}

.sidebar-collapse .main-sidebar {
  background-image: linear-gradient(180deg, #ff736b, #ff2260) !important;
  background-size: auto !important;
  border-radius: 50% 50% 50% 50% / 3% 3% 3% 3% !important;
}
.main-sidebar {
  background-image: linear-gradient(180deg, #ff736b, #ff2260) !important;
  background-size: auto !important;
}
.nav-sidebar li a p,
.nav-sidebar li a .nav-icon {
  color: #f4f6f9 !important;
  font-size: 18px !important;
}
.nav-sidebar li a p {
  font-family: "Source Sans Pro", sans-serif !important;
}
.sidebar-collapse .nav-sidebar li:hover {
  background-color: #edf0f5 !important;
  border-radius: 18% 0% 0% 18% / 43% 0% 0% 43% !important;
  cursor: pointer;
}
.nav-sidebar li:hover {
  background-color: #edf0f5 !important;
  border-radius: 7% 50% 50% 7% / 44% 0% 0% 40% !important;
  cursor: pointer;
}
.nav-sidebar li:hover a p {
  color: #ff3462 !important;
}
.nav-sidebar .nav-item:hover .nav-link .nav-icon {
  color: #ff3462 !important;
}

/***********************  CSS FOR SIDE MENU BAR END ********************/

.text-over-img {
  position: absolute;
  top: 50%;
  left: 40%;
  transform: translate(-50%, -50%);
  color: white;
}
/* ***************************PROGRESS PART CSS START****************************/
/* Total Classes */
.progress1 {
  background-image: linear-gradient(90deg, #553eff, #00f0ff) !important;
}
/* Average Attendance */
.progress2 {
  background-image: linear-gradient(90deg, #423eff, #fe3eff) !important;
}
/* Average Participation */
.progress3 {
  background-image: linear-gradient(90deg, #ff2260, #ff736b) !important;
}
.progress-height {
  height: 4px !important;
}
.prog-text {
  font-size: 14px;
  margin-right: 20px;
  font-family: "Source Sans Pro", sans-serif !important;
  color: #ababab;
}
.tot-percent {
  font-family: "Source Sans Pro", sans-serif !important;
  color: #ababab;
}
.tot-value {
  font-family: "Source Sans Pro", sans-serif !important;
}
/* ***************************PROGRESS PART CSS END***************************/

