.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  /* left: 50px; */
  background-image: linear-gradient(180deg, #ff736b, #ff2260) !important;
  border: 1px solid gray;
  min-width: 200px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown-content .nav-link p {
  color: #f4f6f9 !important;
  font-size: 18px !important;
  text-decoration: none;
}

.dropdown-content a:hover {
  background-color: #edf0f5 !important;
  border-radius: 7% 50% 50% 7% / 44% 0% 0% 40% !important;
  cursor: pointer;
}

.dropdown:hover .dropdown-content {
  display: block;
}

