}
.institute-table {
  /* border-spacing: 8px !important; */
  border-collapse: separate !important;
}
.inst-list-td {
  background-color: #eeeeef;
  color: #838383;
  padding: 8px !important;
  /* border: none !important; */
  border: 1px solid transparent;
}
