}
.pagination-text:hover {
  background-image: linear-gradient(-90deg, #ff736b, #ff2260) !important;
  color: #fff !important;
}
.page-link {
  padding: 4px 8px !important;
  color: #ff2260;
  font-family: "Source Sans Pro", sans-serif !important;
}
.page-link:hover {
  color: #fff !important;
  background-image: linear-gradient(-90deg, #ff736b, #ff2260) !important;
}
.page-item.disabled .page-link {
  color: #ff2260 !important;
}
.page-item.active .page-link {
  background-image: linear-gradient(-90deg, #ff2260, #ff736b) !important;
  color: #fff !important;
  border-color: #dee2e6 !important;
}
.page-link:focus {
  z-index: 3;
  outline: 0;
  box-shadow: none !important;
}
.inst-list-search {
  padding: 0px 8px;
  border: 1px solid #b5b5b5 !important;
  border-radius: 2%;
  color: #838383 !important;
}
.inst-list-search:focus {
  outline-color: #838383 !important;
}
.inst-list-search-div {
  padding: 4px 20px;
  background-image: linear-gradient(90deg, #ff736b, #ff2260) !important;
  border-radius: 5%;
  margin-right: 8px;
}
.serach-icon-inst-list {
  color: #fff;
  font-size: 16px;
}
